<template>
  <div class="user-edit-container">
    <!-- Header 区域 -->
    <van-nav-bar title="编辑资料" left-arrow @click-left="$router.back()" fixed />

    <!-- 用户资料 -->
    <van-cell-group class="action-card">
      <van-cell title="头像" is-link center>
        <template #default>
          <van-image round class="avatar" :src="profileObj.photo" @click="imageClickFn" />
          <!-- file 选择框 -->
          <input type="file" ref="iptFile" v-show="false" accept="image/*" @change="onFileChange" />
        </template>
      </van-cell>
      <van-cell title="名称" is-link @click="nameClickFn">
        {{ profileObj.name }}
      </van-cell>
      <van-cell title="生日" is-link @click="birthdayClickFn">
        {{ profileObj.birthday }}
      </van-cell>
    </van-cell-group>

    <!-- 姓名修改的弹窗 -->
    <van-dialog v-model="show" title="修改名称" show-cancel-button :before-close="beforeCloseFn">
      <!-- 输入框 -->
      <input type="text" v-selfFocus v-model="userName">
    </van-dialog>

    <!-- 日期选择控件弹出层的遮罩组件 -->
    <van-popup v-model="dateShow" position="bottom" style="height: 50%" round>
      <!-- 日期选择控件 -->
      <van-datetime-picker v-model="currentDate" type="date" title="选择出生日期" :min-date="minDate" :max-date="maxDate"
        :show-toolbar="true" @cancel="cancelFn" @confirm="confirmFn" />
    </van-popup>
  </div>
</template>

<script>
import { userProfileAPI, updateUserPhotoAPI, updateProfileAPI } from '@/api/index'
import { Notify } from 'vant'

import { formatDate } from '@/utils/date'

export default {
  name: 'UserEdit',
  data() {
    return {
      profileObj: {},
      show: false, // 点击姓名弹出的弹框
      userName: '',
      userBirthday: '',
      minDate: new Date(1921, 0, 1), // 日期选择控件最小范围
      maxDate: new Date(),
      currentDate: new Date(2022, 5, 16),
      dateShow: false
    }
  },
  methods: {
    // 为了达到选择文件的目的，给头像图片关联一个type类型是"file"的input，input表单状态改变事件触发该方法
    async onFileChange(e) {
      if (e.target.files.length === 0) {
        return
      }
      // 创建FormData格式的对象
      const theFd = new FormData()
      // 向对象中假如名为photo的键值对
      theFd.append('photo', e.target.files[0])
      const { data: res } = await updateUserPhotoAPI(theFd)
      this.profileObj.photo = res.data.photo
    },
    // 点击图片时触发
    imageClickFn() {
      // 获取到那个input标签
      // 模拟该标签的点击事件，这样就能实现点击头像相当于点击表单进行传文件了
      this.$refs.iptFile.click()
    },
    // 姓名的点击事件
    nameClickFn() {
      this.show = true
      this.userName = this.profileObj.name
    },
    // 姓名修改弹出框关闭前的回调函数(参数一表示点击的取消汉室确定，参数二是回调函数)
    async beforeCloseFn(action, done) {
      if (action === 'confirm') {
        // 点击的确定
        // 正则英文数字中文匹配
        const reg = /^[a-zA-Z0-9\u4e00-\u9fa5]{1,9}$/
        if (reg.test(this.userName) === true) {
          // 通过校验，关闭弹窗，否则提示用户且不关闭弹窗
          // 发起修改用户名的请求
          await updateProfileAPI({ userName: this.userName, birthday: this.userBirthday })
          this.profileObj.name = this.userName
          // 关闭弹窗
          done()
        } else {
          done(false)
          Notify({ type: 'warning', message: '用户名只能是1-9位的中英数字组合' })
        }
      } else {
        // 点击的取消，关闭弹窗
        done()
      }
    },
    // 生日点击修改事件
    birthdayClickFn() {
      this.dateShow = true
      this.currentDate = new Date(this.profileObj.birthday)
    },
    // 取消生日弹出面板
    cancelFn() { this.dateShow = false },
    // 确定生日面板
    async confirmFn() {
      this.userBirthday = formatDate(this.currentDate)
      await updateProfileAPI({ userName: this.userName, birthday: this.userBirthday })
      this.profileObj.birthday = this.userBirthday
      this.dateShow = false
    }
  },
  async created() {
    const { data: res } = await userProfileAPI()
    this.profileObj = res.data
  }
}
</script>

<style lang="less" scoped>
.user-edit-container {
  padding-top: 46px;

  .avatar {
    width: 50px;
    height: 50px;
  }
}

.van-cell__value {
  color: #333;
}

/deep/.van-dialog__content {
  text-align: center;

  input {
    padding: 0;
    outline: none;
    border: none;
    text-align: center;
  }
}
</style>
